@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  scroll-behavior: smooth;
}

.section1{
background-color: #001629;
height: 100vh;
margin-top: 0px;
padding: 3%;

}

.intro{

  height: 100%;

}
.intro-right{
  color: #C70039;
  background-color:#49FF00;
  background:url(/my-portfolio/static/media/bacground.969cd80a.jpg);
  padding:0px;
  background-repeat:no-repeat;
  background-size:cover;
  height:100%;


}
.pic{
padding:0px;
}
.intro-left{

  font-size: 5rem;
  border: 4px groove #C70039;
  border-right: 0px;
  font-weight: bold;
  color:white;
  -webkit-align-items: center;
          align-items: center;
}
.animation-box {
  width: 75%;
  height: 15.5rem;
  background: transparent;
  margin: 0 auto;
  overflow: hidden;
  position: relative;
}

.animation-container {
  width: 1000rem;
  height: 30rem;
}

@-webkit-keyframes topFadeOut {
  0% {
    position: absolute;
    top: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    top: 25%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes topFadeOut {
  0% {
    position: absolute;
    top: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    top: 25%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes bottomFadeOut {
  0% {
    position: absolute;
    bottom: -5rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    bottom: 25%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }

}

@keyframes bottomFadeOut {
  0% {
    position: absolute;
    bottom: -5rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    bottom: 25%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }

}

@-webkit-keyframes topFadeOutVertical {
  0% {
    position: absolute;
    top: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    top: 45%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@keyframes topFadeOutVertical {
  0% {
    position: absolute;
    top: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    top: 45%;
    opacity: 1;
  }

  100% {
    opacity: 0;
  }
}

@-webkit-keyframes rightFadeInOut {
  0% {
    position: absolute;
    right: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    right: 10rem;
    opacity: 1;
  }

  100% {
    opacity: 0;
    right: 10rem;
  }
}

@keyframes rightFadeInOut {
  0% {
    position: absolute;
    right: -3rem;
    opacity: 0;
  }

  75% {
    position: absolute;
    right: 10rem;
    opacity: 1;
  }

  100% {
    opacity: 0;
    right: 10rem;
  }
}

@-webkit-keyframes fadeInOut {
  0% {
    opacity: 0;
  }

  45% {
    opacity: 1;
  }

  100% {
    opacity: 0%;
  }
}

@keyframes fadeInOut {
  0% {
    opacity: 0;
  }

  45% {
    opacity: 1;
  }

  100% {
    opacity: 0%;
  }
}

.first-text {
  font-size: 4.4rem;
  position: absolute;
  left: 2.5rem;
  top: 5rem;
  opacity: 0;
  -webkit-animation-name: topFadeOut;
          animation-name: topFadeOut;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  color: white;
}

.second-text {
  font-size: 3.4rem;
  position: absolute;
  top: 8.5rem;
  opacity: 0;
  -webkit-animation-name: rightFadeInOut;
          animation-name: rightFadeInOut;
  -webkit-animation-delay: 3s;
          animation-delay: 3s;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  color: white;
}

.third-text {
  -webkit-transform: rotate(-90deg);
          transform: rotate(-90deg);
  font-size: 2rem;
  position: absolute;
  left: 45%;
  top: 45%;
  opacity: 0;
  -webkit-animation-name: topFadeOutVertical;
          animation-name: topFadeOutVertical;
  -webkit-animation-delay: 7.5s;
          animation-delay: 7.5s;
  -webkit-animation-duration: 6s;
          animation-duration: 6s;
  color: white;
  white-space: nowrap;
}

.fourth-text {
  font-size: 3.4rem;
  position: absolute;
  top: 10.5rem;
  left: 10%;
  opacity: 0;
  color: white;
  -webkit-animation-name: fadeInOut;
          animation-name: fadeInOut;
  -webkit-animation-delay: 8.5s;
          animation-delay: 8.5s;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
}

.fifth-text {
  font-size: 4.4rem;
  position: absolute;
  left: 5%;
  bottom: 25%;
  opacity: 0;
  color: white;
  -webkit-animation-name: bottomFadeOut;
          animation-name: bottomFadeOut;
  -webkit-animation-delay: 10s;
          animation-delay: 10s;
  -webkit-animation-duration: 9s;
          animation-duration: 9s;
}
@media only screen and (max-width: 1100px) {
  .section1{
  background-color: #001629;
  height: auto;
  margin-top: 0px;
  padding: 3%;

  }
  .intro-left{
    padding-top: 35px;
    border: 4px groove #C70039;
    border-bottom: 0px;
    font-size: 5vh;
    font-weight: bold;
    color:white;
    -webkit-align-items: center;
            align-items: center;
    height: 45vh;
  }
  .intro-right{
    color: #C70039;
    background-color:#49FF00;
    background-image:url(/my-portfolio/static/media/bacground.969cd80a.jpg);
    background-position:center;
    background-repeat:no-repeat;
    background-size: 100% auto;
    height: 50vh;


  }
}

